@import url(https://fonts.googleapis.com/css2?family=Carattere&family=Poppins:ital,wght@0,200;0,300;0,400;1,500&family=Raleway:wght@300;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
	/* font-family: 'Carattere', cursive;
	font-family: 'Poppins', sans-serif; */
	font-family: 'Raleway', sans-serif;
	font-size: 18px;
	line-height: 25px;
}


/**************** 2. Top Header Area ********/
.headerText{
	font-family: 'Carattere', cursive;
	text-align: center;

}
.headerText h2 {
    color: #ff35ae;
    font-size: 46px;
    margin-top: 25px;
    font-weight: bold;
}
.logo{
	text-align: center;
}
.logo img {
    height: 113px;
    width: 286px;
    border-radius: 50%;
}
.header-btn{
	text-align: center;
}
.header-btn button {
    background-color: #FF35AE;
    color: #fff;
    padding: 15px 25px;
    margin-top: 26px;
}
/********************Background charity ************************/
.background-charity{
	height: 750px;
	background-position:center bottom;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	position: relative;
    background-image: url(/static/media/carity-3.ac813720.jpg);
    
}
.background-style{
	background:linear-gradient(0,0,0,0.5)
}

.header-inner-text{
    position: absolute;
    top: 200px;
    right: 10%;
	
}

.header-inner-text h1 {
    font-size: 56px;
    color: #fff;
    font-family: 'Raleway';
    line-height: 74px;
}

.header-inner-text button {
    background-color: #FF35AE;
    color: #fff;
    padding: 15px 41px;
    margin-top: 0px;
    border: 0px;
    border-radius: 27px;
}
.amar{
              background: linear-gradient(rgb(37 35 37 / 81%),rgb(154 16 16 / 50%));
               width: 100%;
               height: 100%;
}
/************ Donar Organizations ***************/
.d-org {
    text-align: center;
    margin-top: 50px;
    text-shadow: 1px 2px 3px #ff35ae;
    letter-spacing: 4px;
}
.social-icon{
text-align: center;
margin-bottom: 25px;
}
.card-body ul>li{
	display: inline;
	margin-right: 25px;
}

.card-body button {
    background-color: #FF35AE;
    color: #fff;
    padding: 10px 40px;
    border-radius: 17px;
    text-align: center;
    width: 88%;
    margin-left: 20px;
    border: 0px;
}
.card-title{
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
}

.total-budget {
    background-color: #ffecf0;
    height: auto!important;
    padding: 100px;
    text-shadow: 4px 3px 7px #c39fbd;
	height: 500px;
}
.total-budget button {
    background-color: #af106f;
    color: #fff;
    padding: 15px 25px;
    text-align: center;
    width: 59%;
    border-radius: 37px;
    margin-left: 43px;
    border: 0px;
}
.total{
    margin-top: 15px;
}

